.personajes-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.personaje {
  margin-bottom: 20px;
  text-align: center;
  border: 1px solid black;
  border-radius: 20px;
  width: fit-content;
  height: fit-content;
  margin-left: 10px;
  margin-right: 10px;
  background-color: #e0dede;
}

.personaje p{
  margin-top: 5px;
  margin-bottom: 5px;
}

.personaje img{
  width: 70%;
  height: 70%;
}


/* Media query para dispositivos m√≥viles */
@media (max-width: 768px) {
  .personajes-list {
    align-items: center;
  }

  .personaje {
    width: fit-content;
    height: fit-content;
  }
}

